.character-list {
  width: 80%;
  border: 1px solid #000;
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.character-item {
  width: 25%;
  height: 382px;
  background-color: #949393;
  border: 1px solid #000;
  border-radius: 4px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 5px;
  padding: 5px;
  list-style-type: none;
  display: flex;
}

.character-item img {
  width: 150px;
  border-radius: 50%;
}

.loading-button {
  margin: 25px auto;
  display: block;
}

